*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}



:root {
  --general-font: "DM Sans", sans-serif;
  --heading-font: "Titan One", cursive;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --Ice-cream-color: #ffb8ca;
  --Brand-color: #d41443;
  --general-white: #fff;
}

.bubbles {
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.bubble {
  opacity: .5;
  background: #f1f1f1;
  border-radius: 50%;
  animation: flying 10s ease-in infinite;
  position: absolute;
  bottom: -75%;
}

.bubble:nth-child(1) {
  width: 40px;
  height: 40px;
  animation-duration: 8s;
  left: 10%;
}

.bubble:nth-child(2) {
  width: 20px;
  height: 20px;
  animation-duration: 5s;
  animation-delay: 1s;
  left: 20%;
}

.bubble:nth-child(3) {
  width: 50px;
  height: 50px;
  animation-duration: 10s;
  animation-delay: 2s;
  left: 35%;
}

.bubble:nth-child(4) {
  width: 80px;
  height: 80px;
  animation-duration: 7s;
  animation-delay: 0s;
  left: 50%;
}

.bubble:nth-child(5) {
  width: 35px;
  height: 35px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 55%;
}

.bubble:nth-child(6) {
  width: 45px;
  height: 45px;
  animation-duration: 8s;
  animation-delay: 3s;
  left: 65%;
}

.bubble:nth-child(7) {
  width: 25px;
  height: 25px;
  animation-duration: 7s;
  animation-delay: 2s;
  left: 75%;
}

.bubble:nth-child(8) {
  width: 80px;
  height: 80px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 80%;
}

.bubble:nth-child(9) {
  width: 15px;
  height: 15px;
  animation-duration: 9s;
  animation-delay: 0s;
  left: 70%;
}

.bubble:nth-child(10) {
  width: 50px;
  height: 50px;
  animation-duration: 5s;
  animation-delay: 3s;
  left: 85%;
}

.modal-buy {
  width: 100vw;
  height: 100vh;
  pointer-events: auto;
  z-index: 200;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: transform .2s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content-buy {
  max-width: 528px;
  border: 3px solid var(--Brand-color);
  background: #fff;
  border-radius: 30px;
  margin: 0 auto;
  position: relative;
}

.is-hidden-buy {
  visibility: hidden;
  transform: translateY(100%);
}

.close-btn-buy {
  width: 30px;
  height: 30px;
  color: #000;
  cursor: pointer;
  transition: var(--transition);
  background: none;
  border: none;
  outline: none;
  align-items: center;
  display: flex;
  position: absolute;
  top: 10px;
  right: 8px;
}

.close-x {
  color: var(--Brand-color);
  transition: var(--transition);
}

.close-btn-buy:hover {
  rotate: 180deg;
}

.form-buy {
  flex-direction: column;
  gap: 10px;
  padding: 40px 40px 20px;
  display: flex;
}

.form-group-buy {
  flex-direction: column;
  gap: 4px;
  padding-top: 10px;
  display: flex;
  position: relative;
}

.form-group-buy .input-modal-buy {
  color: var(--Brand-color);
  font-family: var(--general-font);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 50px;
  padding: 12px 16px;
}

.input-modal-buy::-webkit-outer-spin-button, .input-modal-buy::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input-modal-buy:hover, .input-modal-buy:focus {
  border: 1px solid var(--Ice-cream-color);
  box-shadow: none;
  outline-style: none;
}

.input-modal-buy::-ms-input-placeholder {
  color: rgba(33, 33, 33, .2);
}

.input-modal-buy::placeholder {
  color: rgba(33, 33, 33, .2);
}

.input-modal-buy:hover::-ms-input-placeholder, .input-modal-buy:focus::-ms-input-placeholder {
  color: var(--Ice-cream-color);
}

.input-modal-buy:hover::placeholder, .input-modal-buy:focus::placeholder {
  color: var(--Ice-cream-color);
}

.form-group-buy .order-area {
  resize: none;
  box-shadow: none;
  color: var(--Brand-color);
  font-family: var(--general-font);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 25px;
  outline-style: none;
  padding: 12px 16px;
}

.form-group-buy .order-area:hover, .form-group-buy .order-area:focus {
  border: 1px solid var(--Ice-cream-color);
}

.form-group-buy .order-area::-ms-input-placeholder {
  color: rgba(33, 33, 33, .2);
  font-size: 16px;
}

.form-group-buy .order-area::placeholder {
  color: rgba(33, 33, 33, .2);
  font-size: 16px;
}

.form-group-buy .order-area:hover::-ms-input-placeholder, .form-group-buy .order-area:focus::-ms-input-placeholder {
  color: var(--Ice-cream-color);
}

.form-group-buy .order-area:hover::placeholder, .form-group-buy .order-area:focus::placeholder {
  color: var(--Ice-cream-color);
}

.link-hero-button {
  align-items: center;
  padding: 10px;
  display: flex;
}

.btn-products {
  color: var(--general-white);
}

.btn-about {
  color: var(--Brand-color);
}

.input-icon {
  width: 18px;
  height: 18px;
  position: absolute;
  bottom: 14px;
  left: 14px;
}

.form-title {
  color: var(--Brand-color);
  text-align: center;
  font-family: var(--heading-font);
  letter-spacing: .6px;
  padding-bottom: 20px;
  font-size: 20px;
}

.form-group-buy.terms-conditions-checkbox {
  cursor: pointer;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.checkboxes {
  width: 16px;
  height: 16px;
  position: relative;
}

.form-group-buy .checkbox {
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
}

.form-group-buy .checkbox-checked {
  width: 16px;
  height: 16px;
  visibility: hidden;
  position: absolute;
  left: 0;
}

.terms-conditions-checkbox input {
  cursor: pointer;
  display: none;
}

.form-group-buy input:checked ~ .checkboxes .checkbox-checked {
  visibility: visible;
}

.form-group-buy .terms-info {
  color: #757575;
  letter-spacing: .42px;
  margin-left: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.form-group-btn {
  justify-content: center;
  display: flex;
}

.terms-conditions-checkbox {
  margin-top: 10px;
  margin-bottom: 20px;
}

.form-group-buy .terms-and-conditions {
  color: var(--Brand-color);
  letter-spacing: .42px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: underline;
}

.modal-buy-items {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.modal-buy-item1 {
  background: #ffa5ba;
  border-radius: 10px;
  align-items: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.modal-buy-item2 {
  background: #f0d1a5;
  border-radius: 10px;
  align-items: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.modal-buy-item3 {
  background: #c2e297;
  border-radius: 10px;
  align-items: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.modal-buy-item-amount {
  width: 40px;
  text-align: center;
  border-style: hidden;
  border-radius: 10px;
  outline-style: none;
  padding: 5px;
}

.modal-buy-item-name {
  color: #fff;
}

@keyframes flying {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }

  50% {
    transform: translateX(100px);
  }

  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}

header a {
  color: inherit;
  transition: var(--transition);
  text-decoration: none;
}

header a:visited {
  color: inherit;
}

header ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

header {
  height: 502px;
  width: 100%;
  background: var(--Ice-cream-color);
  z-index: 2;
  position: relative;
  overflow-x: hidden;
}

.wrapper {
  width: 100%;
  background: linear-gradient(45deg, #e17992, #ffbed1, #e17992, #f7d6b1, #e17992) 0 0 / 300% 100%;
  animation: gradient 16s linear infinite alternate;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

.header-container-nav {
  z-index: 3;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px 0;
  display: flex;
}

.menu-button {
  background: none;
  border: none;
  display: flex;
}

.submit-btn-red {
  background: var(--Brand-color);
  cursor: pointer;
  font: var(--general-font);
  color: var(--general-white);
  text-align: center;
  height: 44px;
  border: none;
  border-radius: 22px;
  padding: 0 43px;
  font-size: 16px;
  font-weight: 700;
}

.menu-pc, .header-buttons .submit-btn {
  display: none;
}

.submit-btn-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.submit-btn:hover, .submit-btn:focus {
  transition: var(--transition);
  background: #ecebeb;
  transform: translateY(-3px);
}

.submit-btn:active {
  background: var(--general-white);
  scale: 95%;
}

.submit-btn-red:hover, .submit-btn-red:focus {
  color: var(--Brand-color);
  background: var(--general-white);
  border: 2px solid var(--Brand-color);
  transition: var(--transition);
  transform: translateY(-3px);
}

.hero-buttons {
  gap: 9px;
  margin-top: 20px;
  display: flex;
}

.hero-btn {
  background: var(--general-white);
  cursor: pointer;
  font: var(--general-font);
  color: var(--Brand-color);
  text-align: center;
  height: 34px;
  border: none;
  border-radius: 22px;
  padding: 0 20px;
  font-size: .6em;
  font-weight: 400;
}

.hero-btn-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-btn:hover, .hero-btn:focus {
  transition: var(--transition);
  background: #ecebeb;
  transform: translateY(-3px);
}

.hero-btn:active {
  background: var(--general-white);
  scale: 95%;
}

.hero-btn-red {
  background: var(--Brand-color);
  cursor: pointer;
  font: var(--general-font);
  color: var(--general-white);
  text-align: center;
  height: 34px;
  border: none;
  border-radius: 22px;
  padding: 0 20px;
  font-size: .6em;
  font-weight: 400;
}

.hero-btn-red-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-btn-red:hover, .hero-btn-red:focus {
  transition: var(--transition);
  background: #be0e3a;
  transform: translateY(-3px);
}

.hero-btn-red:active {
  background: var(--Brand-color);
  scale: 95%;
}

.arrow-icon {
  height: 8px;
  width: 4px;
}

.menu-drawer-navigation {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-drawer {
  width: 100%;
  margin-top: 19px;
}

.menu-drawer .link:hover, .menu-drawer .link:active {
  color: var(--Brand-color);
}

.menu-drawer li {
  color: var(--general-white);
  font-size: 1em;
  font-weight: 700;
  font-family: var(--general-font);
  border-top: 1px solid #e18298;
  padding: 13px 0 13px 20px;
}

.menu-drawer li.last-link {
