}

.js-menu-container {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #e17992;
  flex-direction: column;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

.js-menu-container .js-close-menu {
  color: var(--general-white);
  cursor: pointer;
  background: none;
  border: none;
  align-self: flex-end;
  margin-top: 20px;
  margin-right: 20px;
}

.js-menu-container .js-close-menu:hover, .js-menu-container .js-close-menu:focus {
  color: var(--Brand-color);
  box-shadow: none;
  background: none;
}

.hero-image {
  height: 410px;
  z-index: -1;
  cursor: url("tongue.4626c7f3.png"), auto;
  animation: slideInFromLeft .7s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 45%;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100vw);
  }

  100% {
    transform: translateX(0);
  }
}

.elipse {
  width: 345px;
  height: 345px;
  z-index: -2;
  background: #ffa5ba;
  border-radius: 50%;
  margin-left: -80px;
  transition: all 1s;
  animation: elipseScale .7s;
  display: flex;
  position: absolute;
  bottom: 75px;
  left: 45%;
  overflow: hidden;
}

@keyframes elipseScale {
  0% {
    scale: 0%;
  }

  100% {
    scale: 100%;
  }
}

.elipse:hover {
  overflow: hidden;
  transform: scale(130%);
}

.header-title {
  margin-top: 136px;
  margin-left: 20px;
}

.milk-header, .right-header-container {
